import { subscribeToNewsletter } from '../../api/services/homeService';
import { redirect } from 'next/navigation';

// Move server action outside the component
async function handleSubscription(formData) {
    'use server';

    const email = formData.get('email');

    // Enhanced email validation
    if (!email || !email.trim()) {
        redirect('?message=Please enter an email address&type=error');
    }

    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!emailRegex.test(email.trim())) {
        redirect('?message=Please enter a valid email address&type=error');
    }

    try {
        const result = await subscribeToNewsletter(email.trim());

        // Handle different response structures
        if (result && result.rs === 1 && result.res && result.res.ResponseStatus === 1) {
            const successMessage = result.res.ResponseMessage || 'Successfully subscribed to newsletter!';
            redirect(`?message=${encodeURIComponent(successMessage)}&type=success`);
        } else if (result && result.rs === 0) {
            // Handle API error response
            const errorMessage = result.res?.ResponseMessage || 'Subscription failed. Please try again.';
            redirect(`?message=${encodeURIComponent(errorMessage)}&type=error`);
        } else {
            // Handle unexpected response structure
            redirect('?message=Subscription failed. Please try again.&type=error');
        }
    } catch (error) {
        console.error('Newsletter subscription error:', error);

        // More specific error handling
        if (error.message.includes('HTTP error')) {
            redirect('?message=Service temporarily unavailable. Please try again later.&type=error');
        } else if (error.message.includes('network') || error.message.includes('fetch')) {
            redirect('?message=Network error. Please check your connection and try again.&type=error');
        } else {
            redirect('?message=Something went wrong. Please try again.&type=error');
        }
    }
}

export default function NewsletterSubscription({ variant = 'full', searchParams = {} }) {
    const message = searchParams?.message;
    const type = searchParams?.type;

    // Compact version 
    if (variant === 'compact') {
        return (
            <div className="w-full max-w-md mx-auto">
                {message && (
                    <div className={`mb-3 p-3 rounded text-sm ${type === 'success'
                        ? 'bg-green-100 text-green-700 border border-green-200'
                        : 'bg-red-100 text-red-700 border border-red-200'
                        }`}>
                        {decodeURIComponent(message)}
                    </div>
                )}

                <form action={handleSubscription} className="flex gap-0">
                    <input
                        type="email"
                        name="email"
                        placeholder="Type your Email"
                        required
                        className="flex-1 px-4 py-2.5 border border-gray-300 rounded-l-md focus:outline-none focus:ring-2 focus:ring-green-500 focus:border-transparent text-gray-700 placeholder-gray-400 text-sm"
                    />
                    <button
                        type="submit"
                        className="bg-green-600 hover:bg-green-700 text-white font-medium px-6 py-2.5 rounded-r-md transition-colors duration-200 focus:outline-none focus:ring-2 focus:ring-green-500 text-sm whitespace-nowrap"
                    >
                        Subscribe
                    </button>
                </form>
            </div>
        );
    }

    // Full section version 
    return (
        <div className="w-full bg-gradient-to-r from-green-50 to-green-100 py-12 px-6">
            <div className="max-w-6xl mx-auto">
                <div className="bg-white rounded-lg shadow-sm p-8">
                    <h2 className="text-2xl font-bold text-gray-800 mb-3">
                        Subscribe to our Newsletter
                    </h2>
                    <p className="text-gray-600 mb-6">
                        Stay updated with the latest news and updates from the agricultural world.
                    </p>

                    {message && (
                        <div className={`mb-4 p-4 rounded-lg ${type === 'success'
                            ? 'bg-green-100 text-green-700 border border-green-200'
                            : 'bg-red-100 text-red-700 border border-red-200'
                            }`}>
                            {decodeURIComponent(message)}
                        </div>
                    )}

                    <form action={handleSubscription} className="flex flex-col sm:flex-row gap-4">
                        <input
                            type="email"
                            name="email"
                            placeholder="Your email address"
                            required
                            className="flex-1 px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-green-500 focus:border-transparent text-gray-700 placeholder-gray-400"
                        />
                        <button
                            type="submit"
                            className="bg-green-600 hover:bg-green-700 text-white font-semibold px-8 py-3 rounded-lg transition-colors duration-200 focus:outline-none focus:ring-2 focus:ring-green-500 focus:ring-offset-2 whitespace-nowrap"
                        >
                            Subscribe
                        </button>
                    </form>
                </div>
            </div>
        </div>
    );
}
